// DESKTOP 
section.presentation-board {
  & > div {
    border-bottom: 1px solid rgba(125, 125, 125, 0.3);
    margin-top: 20px;
    padding-bottom: 20px;
    width: 100%;
    div.social-network{
      margin-top: 20px;
      h4.what-am-i {
        font-weight: 400;
      }
      ul {
        display: flex;
        justify-content: space-around;
        margin-top: 20px;
        li {
          width: 24px;
          height: 24px;
          padding: 15px;
          display: flex;
          justify-content: center;
          align-items: center;
          background-color: var(--dyellow);
          color: var(--lblack);
          border-radius: var(--circle);
          a {
            text-decoration: none;
          }
        }
      }
    }
    h3, h4{
      margin-bottom: 15px;
      font-weight: 500;
    }
    p {
      font-weight: 300;
      color: var(--gray);
    }
    div.languages-container,
    div.skill{
      p.percent {
        display: flex;
        justify-content: space-between;
      }
    }
    span.jauge{
      display: inline-block;
      width: 100%;
      height: 6px;
      padding:1px;
      border:1px solid var(--dyellow);
      border-radius: 25px;
      margin-top: 5px;
      margin-bottom: 8px;
      span.interne{
        display: block;
        height:2px;
        background-color: var(--dyellow);
      }
    } 
  }
  button.download-cv{
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border: none;
    padding: 8px 15px 8px 15px;
    font-weight: bold;
    width: 75%;
    background-color: var(--dyellow);
  }
}
